		methods:{
			// 网络请求
			getNews:function(){
				page=1
				uni.showNavigationBarLoading()
				uni.request({
				header: {
					'sessionId': 'fa2e5c5698b0480eb68ec05fa9a35a3d' //自定义请求头信息
				},
				url:"http://192.168.1.22:30917/spMgRegulate/getInfo?regulateid=1199658109873885184",
					success:function(res){
						console.log(res);
						uni.stopPullDownRefresh();
						uni.hideNavigationBarLoading();
						page++;
					}
				})
			},
			
			getMoreNews:function(){
				if(_self.lodingText=='加载完毕!'){return false}
				_self.lodingText='加载更多…';
				uni.showNavigationBarLoading();
				uni.request({
					url:"fa2e5c5698b0480eb68ec05fa9a35a3d",
					success:function(res){
						console.log(res);
						if(res==null){
							_self.lodingText='加载完毕!'
							return false;
						}
						var newsList=res.data;
						// 累加不覆盖进行下拉加载
						_self.newsList=_self.newsList.concat(newsList);
						uni.stopPullDownRefresh();
						page++;
					}
				})
			}
		
		}
	}
	
	
